import React, { FunctionComponent, useState, useEffect } from "react";
// @ts-ignore
import { fadeInDown } from "react-animations";
// @ts-ignore
import styled, { keyframes } from "styled-components";

import "./Timer.scss";

interface ITimeLeft {
  days?: number;
  hours?: number;
  minutes?: number;
  seconds?: number;
}
interface ComponentProps {
  displayMessage: boolean;
}

const FadeIn = styled.div`
  animation: 6s ${keyframes`${fadeInDown}`};
`;

const IndexPage: FunctionComponent<ComponentProps> = ({ displayMessage }) => {
  const [timeLeft, setTimeLeft] = useState<ITimeLeft>({});
  const [counting, setCounting] = useState(true);

  const getTimeLeft = (): ITimeLeft => {
    const now = new Date();
    const then = new Date(2020, 5, 20, 16); // months start at 0...

    // + operator is the easiest way to convert to UNIX
    const diff = +then - +now;

    if (diff > 0) {
      const timeLeft = {
        days: Math.floor(diff / (1000 * 60 * 60 * 24)),
        hours: Math.floor((diff / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((diff / 1000 / 60) % 60),
        seconds: Math.floor((diff / 1000) % 60),
      };
      return timeLeft;
    }

    if (counting) {
      setCounting(false);
    }
    return {};
  };

  useEffect(() => {
    let ticker = setTimeout(() => {
      setTimeLeft(getTimeLeft());
    }, 1000);
    return () => {
      clearTimeout(ticker);
    };
  });

  const renderTime = (): string => {
    const formatedNumber = (num: number | undefined) => {
      return ("0" + num).slice(-2);
    };

    if (counting) {
      return `${timeLeft.days}d ~ ${formatedNumber(
        timeLeft.hours
      )}h ~ ${formatedNumber(timeLeft.minutes)}m ~ ${formatedNumber(
        timeLeft.seconds
      )}s`;
    } else {
      return "<3";
    }
  };

  return (
    <div className="messagesContainer">
      {displayMessage === true && (
        <FadeIn>
          <div className="messageText">
            <span>We'll be togheter soon...</span>
          </div>
          <div className="timerText">
            <span>{renderTime()}</span>
          </div>
        </FadeIn>
      )}
    </div>
  );
};

export default IndexPage;
